@import url("https://fonts.googleapis.com/css?family=Didact+Gothic");

html {
  width: 100%;
  font-family: "Didact Gothic";
  background: #008ecc;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.App,
.weatherForm {
  width: 100%;
  background: none;
  font-family: "Didact Gothic";
  background: #fff;
  height: 50px;
  border-bottom: 3px #e1c16e solid;
  padding: 10px 10px 10px 16px;
}

.beforeInput {
  margin-left: 40px;
  font-size: 18px;
  color: #e1c16e;
  font-weight: 700;
}

::placeholder {
  font-style: italic;
  font-size: 0.5em;
  color: black;
}

.selectCountry,
.selectState {
  font-family: "Didact Gothic";
  background: none;
  height: 50px;
  width: 20%;
  font-size: 18px;
  margin: 2px 0 10px 0;
  text-align: center;
  color: #000;
}

.location,
.cityName {
  font-family: "Didact Gothic";
  text-align: center;
  width: 20%;
  margin: 0 0 23px 20px;
  font-size: 35px;
  border-radius: 8px;
}

.cityName {
  font-family: "Didact Gothic";
  border: none;
  width: 100%;
  margin-top: 20px;
  font-size: 35px;
  font-weight: bold;
  height: 35px;
  color: #fff;
}

.onloadMsg {
  text-align: center;
  color: #fff;
  font-size: 45px;
  font-weight: 700;
  font-family: "Didact Gothic";
}

.sunImg {
  margin-top: -30px;
  height: 300px;
  width: 300px;
}

.submitLocation {
  background: none;
  width: 29%;
}

.locationName {
  text-align: center;
  font-family: "Didact Gothic";
  font-size: 30px;
}

.searchIcon {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 25px;
  right: 45px;
}

.weatherDetails {
  font-family: "Didact Gothic";
  display: flex;
  flex-direction: column;
  text-align: center;
  color: white;
}

.currentTemperature {
  font-family: "Didact Gothic";
  color: #fff;
  width: 100%;
  font-size: 20px;
  padding: 10px;
  border-radius: 20px;
  margin-bottom: 20px;
}

.weather {
  font-size: 30px;
}

.currentTemp {
  font-size: 40px;
  height: 60px;
}

input,
select {
  font-family: "Didact Gothic";
  width: 29%;
  padding: 0;
  border: none;
  display: inline-block;
  vertical-align: middle;
  color: #000;
}

.bolded {
  font-weight: 700;
  color: #fff;
}

.errorMsg {
  font-family: "Didact Gothic";
  font-size: 18px;
  color: #e1c16e;
  text-align: center;
  margin-top: 0px;
}

.fiveDayForecast {
  font-family: "Didact Gothic";
  display: flex;
  width: 99%;
  flex-direction: column;
  background: #e1c16e;
  border-radius: 10px;
  border: 5px #fff solid;
  padding-left: 23px;
}

.highs,
.lows,
.daysOfTheWeek,
.icons {
  color: black;
  display: flex;
  flex-direction: row;
  font-family: "Didact Gothic";
}

.dailyHigh,
.dailyLow,
.dayOfTheWeek,
.dailyIcon {
  flex: 0 0 20%;
  font-size: 30px;
  text-align: center;
  height: 50px;
  font-family: "Didact Gothic";
}

.dayOfTheWeek {
  font-size: 14px;
  height: 35px;
  font-weight: 700;
}
.dailyIcon {
  height: 100px;
}

/* Responsive */
@media only screen and (min-width: 320px) and (max-width: 768px) {
  .cityName {
    min-width: 502px;
    margin-top: 50px;
    margin-bottom: 100px;
  }

  .beforeInput {
    width: 100%;
    margin: 31%;
  }

  .sunImg {
    margin: -75px;
  }

  .weatherForm {
    height: 160px;
  }

  .searchIcon {
    top: 45px;
    right: -80px;
  }

  .errorMsg {
    margin-top: -30px;
  }

  ::placeholder {
    font-size: 18px;
  }

  .location {
    padding: 6px;
    width: 85%;
    text-align: center;
    margin-bottom: -10px;
  }

  .selectCountry,
  .selectState {
    margin-bottom: 0;
    width: 100%;
    text-align: center;
  }

  .cityName {
    margin-top: 100px;
    margin-bottom: 80px;
  }

  .weatherDetails,
  .weatherForm {
    min-width: 502px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .selectState,
  .selectCountry {
    width: 20%;
    margin: 7px;
    margin-left: -20px;
  }

  .searchIcon {
    position: absolute;
    top: 35px;
    right: -10px;
  }

  ::placeholder {
    font-size: 18px;
  }

  .location {
    width: 30%;
    margin-bottom: 20px;
  }

  .cityName {
    margin-top: 35px;
    margin-bottom: 10px;
  }

  .sunImg {
    margin-top: 20px;
  }

  .weatherForm {
    min-width: 834px;
    height: 50px;
    margin-right: -17px;
  }

  .beforeInput {
    margin: 20px 0 0 10px;
  }
}
